a:link { text-decoration: none; color: #777777;}
a:visited { text-decoration: none; color: #777777;}
a:hover { text-decoration: none; color: #777777;}
a:active { text-decoration: none; color: #777777;}

html {
  overflow-y: scroll;
}

body {
  font-family: 'Open Sans', sans-serif;
  color: #333333;
  background-color: #f1f1f1;
  padding-bottom: 150px;
}

.App{
  max-width: 1248px;
  margin: 0 auto;
}


/* header */
header{
  text-align: center;
  width: 100%;
  background-color: #fff;
}

/* home page styles */

.page-body{
  background-color: #fff;
  padding: 80px 100px;
  font-size: 16px;
  overflow: auto;
  box-sizing: border-box;
}
.page-body * {
  max-width: 1050px;
  box-sizing: border-box;
}

/* navbar */

.navbar{
  display: flex;
  justify-content: center;
  padding: 20px 0;
  font-size: 14px;
}


/* login */

.login__control{
  margin-top: 25px;
}

.login__input {
  width: 100%;
  height: 40px;
  border: 1px solid #d4d0ba;
  margin: 10px 0 20px 0;
}

.submit__module{
  display: flex;
  flex-direction: row;
  justify-content: space-between;
}

.login__button{
  border: 2px solid #199e81;
  cursor: pointer;
  height: 40px;
  padding: 0 40px;
  color: #199e81;
  background-color: #fff;
}

.login__button:hover{
  color: #000;
  border-color: #000
}

.login__links_item{
  line-height: 2;
  box-sizing: border-box;
  list-style-type: none;
  text-align: center;
}

.login__inputbox{
  margin: 15px;
}

/* login password */

.validate-username{
  border: 1px solid #d4d0ba;
  border-radius: 2px;
  height: 40px;
  padding: 0 10px;
  line-height: 40px;
  margin-top: 10px;
}

.validate-button{
  border: 2px solid #d4d0ba;
  background: transparent;
  cursor: pointer;
  display: inline-block;
  height: 40px;
  padding: 0 40px;
  text-transform: uppercase;
  transition: all 0.3s ease-out;
  color: #199e81;
  margin-left: 15px;
}

.register__input{
  border: 1px solid #d4d0ba;
  border-radius: 2px;
  font-weight: 300;
  height: 40px;
  padding: 0 10px;
  width: 100%;
  margin: 15px 0;
}

.register__input__photo{
  display: block;
  margin: 15px 0;
}

.register__deparment{
  margin-left: 15px;
  margin-bottom: 20px;
}

/* npa */

.ql-editor {
  min-height: 50vh;
  font-size: 16px;
}

.ql-editor > p > img{
  max-width: 300px;
  max-height: 200px;
}

.App .ql-snow .ql-tooltip.ql-flip {
  width: 100%;
  left: 17px!important;
  white-space: normal;
}

.npa-post__page > p > img {
  max-width: 450px;
  max-height: 350px;
}

/* search */

.search__inputs{
  display: flex;
  justify-content: space-between;
}

.search__input{
  border: 1px solid #d4d0ba;
  border-radius: 2px;
  font-weight: 300;
  height: 40px;
  padding: 0 10px;
  float: left;
  width: 65%;
}

.search__btn{
  border: 2px solid #1976d2;
  background: transparent;
  cursor: pointer;
  display: inline-block;
  height: 40px;
  padding: 0 40px;
  text-transform: uppercase;
  color: #1976d2;
  margin-left: 15px;
  width: 30%;
}

fieldset{
  box-sizing: border-box;
  border: 1px solid #e5e5e5;
  border-radius: 2px;
  margin: 10px 0;
  padding: 0 20px;
}

.search__fieldset_legend{
  padding: 0 5px;
}

.option-radio, .search__checkbox_label{
  margin-right: 15px;
}

.search__radio, .search__checkbox{
  margin: 0 5px 0 0;
}

.select-uni{
  border: 1px solid #d4d0ba;
  border-radius: 2px;
  height: 40px;
  width: 250px;
  padding: 0 10px;
}

.search__phrases{
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.search-response__wrapper{
  display: flex;
  flex-direction: column;
  grid-gap: 30px;
  flex-wrap: wrap;
}

.response__item {
  padding: 15px;
  border: 1px dashed black;
  width: 100%;
}

/* carousel */

.carousel__content{
  width:  100%;
  height: 100%;
  margin: 0 auto;
  align-content: center;
}

.carousel__text{
  text-align: center;
  margin-top: 150px;
}

.delete__button{
  position: absolute;
  right: 0;
  color: lightgray;
  top: 0;
}

/* support */

.body-box{
  font-family: 'Jost', sans-serif;
  display: flex;
  flex-direction: column;
  align-items: center;
}
.help__header{
  margin: 0 auto;
  display: flex;
  flex-direction: column;
  width: 400px;
}

.help__header-title{
  font-size: 24px;
  color: #363636;
}

.problem-textarea:focus{
    outline: 1px solid #199e81;
}

.form-submit{
    display: flex;
    margin: 0 auto;
    justify-content: center;
    align-items: center;
    width: 140px;
    height: 35px;
    color: #777777;
    border: 1px solid #777777;
    transition-duration: 0.2s;
}

.form-submit:hover{
    border: 1px solid #199e81;
    color: #199e81;
    background: white;
    cursor: pointer;
}

/* adaptivity */

@media all and (max-width: 1248px){
  .App{
    width: 720px;
  }
  .page-body{
    font-size: 10px;

    padding: 40px 40px;
  }
  .navbar{
    font-size: 10px;
    padding: 10px 0;
  }
  .navbar__link{
    padding: 5px 15px;
  }
  .carousel__wrapper{
    width: 600px;
    height: 500px;
    margin: 0 auto;
  }
  .carousel__item{
    width: 290px;
    height: 150px;
  }
  .search__input{
    width: 65%;
  }
  .search__btn{
    width: 30%;
  }
  .help__header-title{
    font-size: 18px;
  }
}

@media all and (max-width: 768px){
  .App{
    width: 480px;
  }
  .page-body{
    font-size: 10px;
    padding: 40px 40px;
  }
  .navbar{
    font-size: 10px;
    padding: 10px 0;
  }
  .navbar__link{
    padding: 5px 10px;
  }
  .carousel__wrapper{
    width: 400px;
    height: 350px;
    grid-gap: 10px;
  }
  .carousel__item{
    width: 195px;
    height: 110px;
  }
  .controls{
    display: flex;
    flex-wrap: wrap;
  }
  .problem-textarea{
    width: 300px;
  }
  .help__header-title, .help__header-description{
    margin-left: 50px;
  }
}

.show-arrow{
  display: inline-block;
  margin-left: 15px;
  width: 25px;
  height: 25px;
  -webkit-transform: rotate(90deg);
          transform: rotate(90deg);
}
.admin-form__wrapper {
  padding-bottom: 20px;
  border-bottom: 1px solid;
  border-color: rgba(0, 0, 0, 0.12);
}

.user__photo{
  width: 70px;
  height: 100px;
}
